@import url(https://fonts.googleapis.com/css2?family=Be+Vietnam:wght@400;500;700;800&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none;
  font-size: 16px;
  font-family: 'Be Vietnam', sans-serif;
}


:root {
    --primary: #fff;
    --pale-red: hsl(13, 100%, 96%);
    --bright-red: hsl(12, 88%, 59%);
    --light-grey: hsl(0, 0%, 98%);
    --grayish-blue: hsl(227, 12%, 61%);
    --dark-blue: hsl(233, 12%, 13%);
}

.btn {
    padding: 8px 25px;
    border-radius: 100px;
    outline: none;
    border: none;
    cursor: pointer;
    white-space: nowrap;
    font-size: 1rem;
}

.btn--primary {
    background-color: hsl(12, 88%, 59%);
    background-color: var(--bright-red);
    color: #fff;
    border: 1px solid hsl(12, 88%, 59%);
    border: 1px solid var(--bright-red);
}

.btn--outline {
    background-color: hsl(12, 88%, 59%);
    background-color: var(--bright-red);
    color: #fff;
    padding: 8px 20px;
    transition: all 0.3s ease-out;
}

.btn--small {
    background-color: hsl(12, 88%, 59%);
    background-color: var(--bright-red);
    color: #fff;
    padding: 8px 10px;
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 30px;
    font-size: 1rem;
    font-weight: 800;
}

.btn--large {
    padding: 15px 26px;
    font-size: 20px;
}

.btn--mobile {
    text-align: center;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out
}

.btn--wide {
    padding: 15px 64px;
    font-size: 1rem;
    font-weight: 800;
}

.btn--large:hover, .btn--medium:hover, .btn--mobile:hover {
    transition: all 0.3s ease-out;
    background: hsl(13, 100%, 96%);
    background: var(--pale-red);
    color: hsl(12, 88%, 59%);
    color: var(--bright-red);
}

.btn--wide:hover {
    color: #fff;
    background-color: hsl(13, 100%, 96%);
    background-color: var(--pale-red);
    transition: all 0.2s ease-out;
}

.white {
    background-color: #ffffff;
    color: hsl(12, 88%, 59%);
    color: var(--bright-red);
    border: none;
    font-weight: 800;
}

.pink {
    background-color: hsl(12, 88%, 59%);
    background-color: var(--bright-red);
    color: #fff;
    border: none;
}

.primary{
    background-color: hsl(12, 88%, 59%);
    background-color: var(--bright-red);
    color: #fff;
    border: none;
}

.primary:hover {
    background-color: hsl(13, 100%, 96%);
    background-color: var(--pale-red);
    color: #242424;
    border: none;
}

.green {
    background-color: #25ce4a;
    color: #fff;
    border: none;
}

.green:hover {
    background-color: #242424;
    color: #fff;
    border: none;
}

.btn-link {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 8px 16px;
    height: 100%;
    width: 100%;
    font-weight: 800;
    font-size: 1rem;
}


.footer-container {
    color: #ffffff;
    background-color: #000;
    justify-content: space-between;
    align-items: center;
    line-height: 2.0;
    padding: 50px 0;
}

.footer__logo {
    align-self: flex-start;
    margin-top: 30px;
    
}

.footer__logo img {
    -webkit-filter: brightness(0) invert(1);
            filter: brightness(0) invert(1);
}

.footer-links {
    display: flex;
    flex-direction: column;
    align-self: flex-start;
    margin: 20px;
    text-align: left;
    box-sizing: border-box;
}

.footer-links a {
    color: #ffffff;
    text-decoration: none;
}

.footer-links a:hover {
    color: #888888;
    transition: 0.3s ease-out;
}

.links_grid_two {
    align-self: flex-start;
}

.footer-input {
    padding: 10px 18px;
    border-radius: 100px;
    margin-right: 10px;
    outline: none;
    border: none;
    font-size: 12px;
    border: 1px solid #fff;
}

.email__newsletter {
    margin-top: 30px;
    align-self: flex-start;
}

.footer-input::-webkit-input-placeholder {
    color: #b1b1b1;
}

.footer-input:-ms-input-placeholder {
    color: #b1b1b1;
}

.footer-input::-ms-input-placeholder {
    color: #b1b1b1;
}

.footer-input::placeholder {
    color: #b1b1b1;
}

.soc-copy {
    width: 100%;
}

.copyright p {
    font-size: 12px;
}

.social-icons a {
    color: #ffffff;
    margin: 5px;
}

.social-icons a:hover {
    color: #888888;
    transition: 0.3s ease-out;
}

.footer_links_justify {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 30%;
}

@media screen and (max-width: 820px) { 
    .footer-links {
        text-align: left;
        margin: 20px;
    }

    .footer {
        flex-direction: column;
        justify-content: center;
        
    }

    .footer_links_justify {
        width: 70%;
        margin: 0 auto;
    }   

    .email__newsletter {
        order: 1;
        align-self: center;
        margin-bottom: 20px;
    }

    .footer_links_justify {
        order: 2;
    }

    .footer__logo {
        order: 3;
        align-self: center;
        margin-bottom: 20px;
    }

    .soc-copy {
        display: flex;
        flex-direction: column;
        margin: 20px auto;
    }
}

.card-design {
    text-align: center;
}
.card-main {
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    overflow-y: auto;
}

.card-grid {
    position: relative;
    flex: 0 0 auto;
    padding: 20px 20px;
    background-color: hsl(0, 0%, 98%);
    margin: 10px; 
    width: 40%;
    text-align: center;
    margin-top: 60px;
}

.card-grid p, h3 {
    font-size: 1rem;
    line-height: 1.5;
    margin-top: 10px;
}

.card-grid img {
    width: 20%;
    margin-top: -70px;
}

.btn-start {
    margin-top: 30px;
}

@media screen and (max-width: 991px) {
    .card-grid {
        width: 100%;
    }
}


.first-footer {
    background-color: hsl(12, 88%, 59%);
    padding: 50px 0;
    margin-top: 100px;
}

.first-footer-items {
    display: flex;
    justify-content: space-between;
    align-self: center;
    align-items: center;
}

.foot-txt {
    width: 50%;
}
.footer-text {
    color: #ffffff;
    font-weight: 800;
    font-size: 3rem;
}


@media screen and (max-width: 700px) {
    .first-footer-items  {
        display: block;
        text-align: center;
        width: 90%;
        margin: 0 auto;
    }
    .foot-txt {
        width: 80%;
        font-size: 2rem;
        margin: auto;
        margin-bottom: 30px;
    }
    .footer-text {
        font-size: 2rem;
    }
}


.grid-text-main {
    background-image: url(/Manage-Landing-Page/static/media/bg-tablet-pattern.b6990a98.svg);
    background-position: -90% -150%;
    background-repeat: no-repeat;
    object-fit: cover;



    padding: 50px 0;
    margin: 0 auto;
}

.main-row {
    align-items: center;
}

.row {
    display: flex;
    flex-wrap: wrap;
    align-content: stretch;
    align-items: center;
    justify-content: space-evenly;
}

.col {
    margin-bottom: 15px;
    padding-right: 15px;
    padding-left: 15px;
    flex: 1 1;
    max-width: 50%;
    flex-basis: 50%;
    align-items: center;
}

.main-grid-head {
    font-weight: 800;
    font-size: 3rem;
    line-height: 1.5;
    color: hsl(228, 39%, 23%);
    max-width: 90%;
    margin: 0 auto;
}

.col-head h3 {
    font-weight: 800;
    font-size: 1.5rem;
    line-height: 1.5;
    color: hsl(228, 39%, 23%);
    margin: 0 auto;
    width: 100%;
}

.main-row p {
    color: #666666;
    line-height: 2;
    width: 80%;
    margin: 20px auto;
    font-size: .9rem;
}

.num {
    color: #ffffff;
    background-color: hsl(12, 88%, 59%);
    border-radius: 100px;
    padding: 5px 15px;
    margin-right: 10px;
}

.main__text-grid {
    align-self: flex-start;
    width: 100%;
}

.main__text-grid p {
    width: 90%;
}

.text-row-two, .text-row-three {
    margin-top: 30px;
}

.col-head {
    display: flex;
}

@media screen and (max-width: 991px) {
    .container {
        padding-right: 30px;
        padding-left: 30px;
    }

    .main-grid-head {
        font-size: 2.5rem;
    }

    .col-head h3 {
        font-size: 1.2rem;
        width: 100%;
    }

    .col-head {
        background-color: hsl(13, 100%, 96%);
        border-radius: 100px;
        padding: 0;
        position: relative;
        overflow-x: hidden;
        width: 100%;
    }

    .main__text-grid {
        text-align: center;
    }
}

@media screen and (max-width: 768px) {

    .grid-text-main {
        background-image: none;
    }
    .hero__text {
        padding-bottom: 65px;
    }

    .col {
        max-width: 100%;
        flex-basis: 100%;
    }

    .col-head h3 {
        font-size: 1rem;
    }

    .main-row p {
        width: 100%;
    }


}
