:root {
    --primary: #fff;
    --pale-red: hsl(13, 100%, 96%);
    --bright-red: hsl(12, 88%, 59%);
    --light-grey: hsl(0, 0%, 98%);
    --grayish-blue: hsl(227, 12%, 61%);
    --dark-blue: hsl(233, 12%, 13%);
}

.navbar-container {
    display: flex;
    justify-content: space-between;
    height: 80px;
    /* position: fixed;
    margin-top: 80px; */

    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    background-color: #fff; */
}

.navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
}

.container {
    z-index: 1;
    width: 100%;
    max-width: 1300px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 50px;
    padding-left: 50px;
}

.navbar-icon {
    margin-right: 0.5rem;
}

.nav-menu {
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    text-align: center;
    justify-content: end;
}

.nav-item {
    height: 80px;
    border-bottom: 2px solid transparent;
}
  
.nav-links {
    color: hsl(233, 12%, 13%);
    color: var(--dark-blue);
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
}

.nav-links:hover {
    color: hsl(227, 12%, 61%);
    color: var(--grayish-blue);
}

.navbar-logo {
    justify-self: start;
    cursor: pointer;
    display: flex;
    align-items: center;
}
  
.fa-bars {
    color: #fff;
}
  
.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {
  .container {
    padding-right: 10px;
    padding-left: 10px;
  }
    .NavbarItems {
      position: relative;
    }

    .navbar {
      margin-top: 15px;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: hsl(12, 88%, 59%);
      left: 0;
      top: 50px;
      height: 100vh;
      opacity: 1;
      transition: all 0.6s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
      color: #ffffff;
    }
  
    .nav-links:hover {
      color: #f00946;
      transform: scale(1.2);
      transition: all 0.3s ease;
    }
  
    .nav-item:hover {
      border: none;
    }
  
    .nav-item {
      width: 100%;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: flex;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 3rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 5rem;
    }
  
    .nav-btn {
      display: none;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 120px;
    }
  }
